          <div class="c_98">未入账房费</div>
          <div class="f84 left">{{ Billdata.crashTotalPay | priceToFixed }}</div>
        </div>
        <div class="w24">
          <div class="c_98">未入账押金</div>
          <div class="f84 left">{{ Billdata.crashPay | priceToFixed }}</div>
        </div>
        <div class="w24">
          <div class="c_98">待退押金</div>
          <div class="f84 left">{{ Billdata.crashDepositPay | priceToFixed }}</div>
        </div>
      </div>
    </div>
