          class="title p5 p65"
          style="background:#fff;border-radius: 8px 8px 0 0;"
        >
          <span
            class="three_color"
            style="background: #02BC6C;"
          ></span>
          <span class="c_000">收款详情</span>
        </div>
        <div
          class="display p5 p65 m2"
          style="font-weight:bold;"
        >
          <div class="w30 left c_000">收款方式</div>
          <div class="w30 w18 left c_000">金额(元)</div>
        </div>
      </div>
      <div
        class="back"
        v-if="Billdata"
      >
        <div class="display p5 border_b">
          <div
            class="w30 left c_000"
            @click="bthShow_1 =! bthShow_1"
          >平台收款
            <van-icon :name="bthShow_1 == false?'arrow':'arrow-down'" />
          </div>
          <div class="w30 w18 left">{{ Billdata.platPay | priceToFixed }}</div>
        </div>
        <div
          v-if="bthShow_1"
          class="no_bold"
        >
          <div class="display p5 border_b">
            <div class="w30 left">哎哟有房</div>
            <div class="w30 c_blue left">{{ Billdata.aiyoPlatPay | priceToFixed }}</div>
          </div>
          <div class="display p5 border_b">
            <div class="w30 left">携程</div>
            <div class="w30 c_blue left">{{ Billdata.xcPlatPay | priceToFixed }}</div>
          </div>
          <div class="display p5 border_b">
            <div class="w30 left">美团</div>
            <div class="w30 c_blue left">{{ Billdata.mtPlatPay | priceToFixed }}</div>
          </div>
          <div class="display p5 border_b">
            <div class="w30 left">飞猪</div>
            <div class="w30 c_blue left">{{ Billdata.fzPlatPay | priceToFixed }}</div>
          </div>
          <div class="display p5 border_b">
            <div class="w30 left">其他平台</div>
            <div class="w30 c_blue left">{{ Billdata.otherPlatPay | priceToFixed }}</div>
          </div>
        </div>
      </div>
      <div
        class="back"
        v-if="Billdata"
      >
        <div class="display p5 border_b">
          <div
            class="w30 left c_000"
            @click="bthShow_2 =! bthShow_2"
          >线下收款
            <!-- <van-icon :name="bthShow_2 == false?'arrow':'arrow-down'" /> -->
          </div>
          <div class="w30 w18 left">{{ Billdata.normalPay | priceToFixed }}</div>
        </div>
        <!-- <div
          v-if="bthShow_2"
          class="no_bold"
        >
          <div class="display p5 border_b">
            <div class="w30 left">现金</div>
            <div class="w30 c_blue left">{{ Billdata.crashPay | priceToFixed }}</div>
          </div>
          <div class="display p5 border_b">
            <div class="w30 left">微信</div>
            <div class="w30 c_blue left">{{ Billdata.wxPay | priceToFixed }}</div>
          </div>
          <div class="display p5 border_b">
            <div class="w30 left">支付宝</div>
            <div class="w30 c_blue left">{{ Billdata.aliPay | priceToFixed }}</div>
          </div>
          <div class="display p5 border_b">
            <div class="w30 left">银行卡</div>
            <div class="w30 c_blue left">{{ Billdata.posPay | priceToFixed }}</div>
          </div>
          <div class="display p5 border_b">
            <div class="w30 left">对公转账</div>
            <div class="w30 c_blue left">{{ Billdata.cooperPay | priceToFixed }}</div>
          </div>
        </div> -->
      </div>
      <div
        class="back"
        v-if="Billdata"
      >
        <div class="display p5 border_b">
          <div
            class="w30 left c_000"
            @click="bthShow_3 =! bthShow_3"
          >非房费收入
            <van-icon :name="bthShow_3 == false?'arrow':'arrow-down'" />
          </div>
          <div class="w30 w18 left">{{ Billdata.otherPay | priceToFixed }}</div>
        </div>
        <div
          v-if="bthShow_3"
          class="no_bold"
        >
          <div class="display p5 border_b">
            <div class="w30 left">延时退房扣押</div>
            <div class="w30 c_blue left">{{ Billdata.autoDepositCutPay | priceToFixed }}</div>
          </div>
          <div class="display p5 border_b">
            <div class="w30 left">查房扣押</div>
            <div class="w30 c_blue left">{{ Billdata.depositCutPay | priceToFixed }}</div>
          </div>
          <div class="display p5 border_b">
            <div class="w30 left">提前退房补差</div>
            <div class="w30 c_blue left">{{ Billdata.extraPay | priceToFixed }}</div>
          </div>
        </div>
      </div>
      <div
        class="back"
        v-if="Billdata"
      >
        <div class="display p5 border_b">
          <div
            class="w30 left c_000"
            @click="bthShow_4 =! bthShow_4"
          >支出款项
            <van-icon :name="bthShow_4 == false?'arrow':'arrow-down'" />
          </div>
          <div class="w30 w18 left">{{ Billdata.outPay | priceToFixed }}</div>
        </div>
        <div
          v-if="bthShow_4"
          class="no_bold"
        >
          <div class="display p5 border_b">
            <div class="w30 left">冲账</div>
            <div class="w30 c_blue left">{{ Billdata.reversePay | priceToFixed }}</div>
          </div>
          <div
            class="display p5 border_b"
            style="border-bottom:none;"
          >
            <div class="w30 left">退款</div>
            <div class="w30 c_blue left">{{ Billdata.refusePay | priceToFixed }}</div>
          </div>
        </div>
      </div>
