      class="back_e2"
      v-if="Billdata"
    >
      <div class="display p5 p65 m2">
        <div class="w30 left c_000">合计</div>
        <div class="w30 w18 left c_000">{{ Billdata.totalPay | priceToFixed }}</div>
      </div>
    </div>
  </div>
</template>
<script>
import { GetBusinessBillRecordNoPaidByhotel } from '@/api/api_business_hotel_and_scale';
export default {
    data() {
        return {
            bthShow_1: true,
            bthShow_2: true,
            bthShow_3: true,
            bthShow_4: true,
            Billdata: null,
            Billorder: null
        };
    },
    created() {
        this.$store.commit('setHeader', '未出账统计');
        this.getBillRecord(this.$route.query.hotelId);
    },
    filters: {
        priceToFixed(price) {
            return Number(price).toFixed(2);
        }
    },
    methods: {
        // 查询酒店未出账账单明细
        getBillRecord(id) {
            if (this.$route.query.isshow == '1') {
                GetBusinessBillRecordNoPaidByhotel(id).done((res) => {
                    this.Billdata = res.data;
                });
            }
        }
    }
};
</script>
<style scoped>
#finance {
    font-size: 16px;
    text-align: left;
    color: #5c5c5c;
    position: fixed;
    top: 56px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
}
.three_color {
    display: inline-block;
    height: 10px;
    width: 4px;
    background: #048fe5;
    border-radius: 2px;
}
.priceBox {
    font-weight: 450;
    background: #fff;
    margin: 8px;
    border-radius: 8px;
}
.handBill {
    display: inline-block;
    width: 50px;
    height: 22px;
    color: #fff;
    font-size: 14px;
    background: #2e7bf7;
    border-radius: 12px;
    display: flex;
    justify-content: center;
    line-height: 23px;
    margin-right: 20px;
}
.back_kong {
    background: #fff;
    color: #5c5c5c;
    border: 1px solid #5c5c5c;
}
.w24 {
    width: 24%;
}
.w62 {
    width: 62%;
    text-align: left;
    display: flex;
    align-items: center;
}
.title {
    font-size: 16px;
}
.display {
    text-align: center;
    display: flex;
    padding: 10px 4px;
    justify-content: space-between;
}
.p6 {
    padding: 0 12px;
}
.mt6 {
    margin-top: 12px;
    margin: 8px;
    border-radius: 8px;
}
.m2 {
    margin: 0 4px;
}
.p5 {
    padding: 10px 0;
}
.p65 {
    padding: 10px 12px;
}
.w30 {
    text-align: center;
    width: 30%;
}
.w18 {
    width: 22%;
}
.c_blue {
    color: #048fe5;
    width: 22%;
}
.f84 {
    font-size: 17px;
    color: #393939;
    font-weight: bold;
}
.c_000 {
    color: #000;
}
.f_20 {
    font-size: 28px;
    color: #000;
    font-weight: bold;
}
.left {
    text-align: left;
}
.border_b {
    border-bottom: 1px solid #e5e5e5;
    margin: 0 16px;
}
.no_bold {
    font-weight: normal;
}
.c_98 {
    font-size: 14px;
    text-align: left;
    color: #989898;
    font-weight: normal;
    padding-bottom: 6px;
}
.back {
    font-weight: bold;
    background: #fff;
    font-size: 14px;
}
.back_e2 {
    background: #e2e2e2;
    font-weight: bold;
    font-size: 17px;
    margin: 0 8px 8px;
    border-radius: 0 0 8px 8px;
    margin-bottom: 20px;
}
.backfff {
    background: #fff;
}
</style>



